img {
  width: 200px;
  height: 200px;
  border-radius: 50%;
}
h1 {
  color: #ff8ca8;
}

.para {
  font-family: "Inconsolata", monospace;
  color: white;
  text-align: center;
  margin: 0px 100px;
}
@media (max-width: 720px) {
  .home-content {
    margin: 0px;
  }
  .para {
    font-size: 16px bold;
    line-height: 30px;
   margin: 10px;
  }
}

@media (min-width: 720px) {
  .home-content {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: 0px 10vw;

  }

  .para {
   
    line-height: normal;
  }
}
