.card {
  background-color: white;
  border-radius: 10px;
  padding: 10px;
  border: 5px;
  box-shadow: 0 0 10px #000000;
  margin: 5px;
  color: #363657;
  font-family: inherit;
}

.card:hover {
  animation: shake 1s;
}
@keyframes shake {
  0% {
    transform: rotate(0deg);
  }

  20% {
    transform: rotate(5deg);
  }

  40% {
    transform: rotate(-5deg);
  }
  50% {
    transform: rotate(5deg);
  }

  70% {
    transform: rotate(-5deg);
  }

  100% {
    transform: rotate(0deg);
  }
}
