.button {
  color: white;
  background-color: #363657;
  width: 150px;
  height: 50px;
  padding: 0px;
  margin: 10px;
  border: 1px solid #585873;
  border-radius: 5px;
  font-family: "Roboto";
  font: bold;
}

.button:hover {
  border-color: #ff8ca8;
  color: #ff8ca8;
  cursor: pointer;
}
